<template>
	<div class="swiper-container" :class="swipeid">
		<div class="swiper-wrapper">
			<slot name="swiper-con"></slot>
		</div>
		<div :class="{'swiper-pagination':pagination}" :style="{'text-align':paginationDirection}"></div>
	</div>
</template>

<script>
	import '../assets/lib/swiper/js/swiper.js'
	export default {
		name:"m-swipe",
		props:{
			swipeid:{
				type:String,
				default:'swipe01'
			},
			effect:{
				type:String,
				default:'slide'
			},
			loop:{
				type:Boolean,
				default:true
			},
			direction:{
				type:String,
				default:'horizontal'
			},
			pagination:{
				type:Boolean,
				default:true
			},
			autoplay:{
				type:Number,
				default:true
			},
			paginationType:{
				type:String,
				default:'bullets'
			},
			paginationDirection:{
				type:String,
				default:'center'
			}
		},
		mounted(){
			var That = this;
			new Swiper('.'+That.swipeid,{
				// 循环
				loop:That.loop,
				// 分页器
				pagination:'.swiper-pagination',
				// 分页类型 fraction,progress,bullets
				paginationType:That.paginationType,
				autoplay:That.autoplay,
				direction:That.direction,
				// 特效 slide,fade,coverflow,cube
				effect:That.effect,
				autoplayDisableOnInteraction:false,
			})
		}
	}
</script>
<style>
	.swiper-container img{
		border-radius:0;
		width: 100%;
		/*max-width: 900px;*/
	}
	.swiper-pagination-bullet-active{
		background: #fff;
	}
</style>


